@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700,800&display=swap&subset=cyrillic);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.todo-item{position:relative;align-items:center;display:grid;grid-template-columns:1fr minmax(150px, 300px) 2fr;box-shadow:0 3px 3px gray;margin:1rem auto;width:30vw;min-width:290px;max-width:600px;transition:.3s;padding:1rem 0}@media all and (max-width: 1370px){.todo-item{padding:0}}.todo-item span{font-weight:bold;transition:.3s;word-wrap:break-word}.todo-item .buttons{justify-self:end;align-self:center;padding-right:.5rem}.todo-item--done{background-color:#9593FF}.todo-item--done span{color:white;word-wrap:break-word}.todo-item input{margin:0 1rem}.todo-subitem{width:25vw;margin:0 auto;display:grid;grid-template-columns:1fr minmax(100px, 300px) 1fr;min-width:290px;max-width:600px;position:relative}.todo-subitem__check{width:2rem}.todo-subitem span{margin:0;font-weight:400;height:1rem;display:inline-block;padding-bottom:.25rem;position:relative;top:50%;transform:translateY(-50%);word-wrap:break-word}.todo-subitem input{margin:0 1rem;align-self:center;height:1.25rem;outline:none;background:none;border:none;border-bottom:1px solid black}:root{box-sizing:border-box;font-size:1rem}*{margin:0;padding:0;box-sizing:inherit;font-family:'Raleway', sans-serif}body{overflow-x:hidden}.button{margin:.5rem}.App{height:100vh;width:100vw;text-align:center;font-family:'Open Sans', sans-serif;overflow-x:hidden;color:#282c34}.App h3{font-family:'Dancing Script', cursive;font-size:3rem;line-height:1;margin:1rem 0 2rem}.App input{font-family:'Open Sans', sans-serif}.App .main-input{outline:none;border:none;border-bottom:1px solid black;padding:0 0 .5rem 1rem;width:40vw;min-width:300px;max-width:700px}.App .isDone{position:absolute;left:0;top:50%;transform:translateY(-50%);pointer-events:none}@media all and (max-width: 375px){.App .isDone{left:1rem}}.App .isDone--todo{left:1rem;padding:0}.App .isDoneCheckbox{position:absolute;left:0;top:50%;transform:translateY(-50%);margin:0;height:1.5rem;width:1.5rem;opacity:0;z-index:2;padding-left:1rem}.App .isDoneCheckbox--todo{left:1rem;padding:0}@media all and (max-width: 375px){.App .isDoneCheckbox{left:1rem}}.App .checkSpan{height:1rem;width:1rem;padding:0;background-color:transparent;border:1px solid gray;position:absolute;left:0;top:50%;transform:translateY(-50%);pointer-events:none;z-index:1}@media all and (max-width: 375px){.App .checkSpan{left:1rem}}.App .checkSpan--todo{left:1rem;padding:0}

